body {
  margin: 0;
  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.social_bar{
	flex-direction: column;
	display: flex;
	justify-content: flex-end;
}
.social_icon{
	cursor: pointer;
	color:#fff;

}
.social_icon:hover{
	color: #dc3545;
}
.banner_full {
	height: 100vh;
	width: 100%;
	background:linear-gradient(0deg, rgba(0,0,0,.3), rgba(0,0,0,.3)), url(/Games-Blog/static/media/battelfield_1_1920x1080.f97a8deb.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center top;
	-webkit-animation: slide 20s infinite;
	        animation: slide 20s infinite; 
	font-family: 'Raleway' ,sans-serif;
}
.banner_h1{
	transition: all 1s ease-in-out
}
.banner_h1:hover{
	opacity: 0;
}
@-webkit-keyframes slide {
	0%{
		background:linear-gradient(0deg, rgba(0,0,0,.3), rgba(0,0,0,.3)), url(/Games-Blog/static/media/assassins.c849a3de.jpg);
		background-position: center;
		background-size: cover;
	}
	33%{
		background:linear-gradient(0deg, rgba(0,0,0,.3), rgba(0,0,0,.3)), url(/Games-Blog/static/media/Forza-horizon_1920x1080.95e8ef63.png);
		background-position: center;
		background-size: cover;
	}
67%{
	background:linear-gradient(0deg, rgba(0,0,0,.3), rgba(0,0,0,.3)), url(/Games-Blog/static/media/hitman.616f689b.jpg);
	background-position: center;
	background-size: cover;
}
}
@keyframes slide {
	0%{
		background:linear-gradient(0deg, rgba(0,0,0,.3), rgba(0,0,0,.3)), url(/Games-Blog/static/media/assassins.c849a3de.jpg);
		background-position: center;
		background-size: cover;
	}
	33%{
		background:linear-gradient(0deg, rgba(0,0,0,.3), rgba(0,0,0,.3)), url(/Games-Blog/static/media/Forza-horizon_1920x1080.95e8ef63.png);
		background-position: center;
		background-size: cover;
	}
67%{
	background:linear-gradient(0deg, rgba(0,0,0,.3), rgba(0,0,0,.3)), url(/Games-Blog/static/media/hitman.616f689b.jpg);
	background-position: center;
	background-size: cover;
}
}
@media only screen and ( max-width: 576px) {
	.banner_h1{
		font-size: 2rem;
		
	}
}
nav a:hover {
	text-decoration: none;
	color:grey;
}
nav a.active{
	color: #fff;
	box-sizing: border-box;
	border-bottom: 2px solid #dc3545;
}
nav a{
	position: relative;
	color:#adb5bd;
	margin-left: 1rem;
	font-family: 'Raleway', sans-serif;
text-transform: uppercase;
}
#brand:hover{
		color:grey;
}
#brand a.active{
	color:#fff!important;
		border-bottom: 2px solid transparent;
}
.sticky_nav{
background: rgb(99,99,96);
background: linear-gradient(159deg, rgba(99,99,96,1) 0%, rgba(47,47,47,1) 100%);
height: 8vh;
}
@media only screen and (max-width: 576px) {
	.sticky_nav{
background: rgb(99,99,96);
background: linear-gradient(159deg, rgba(99,99,96,1) 0%, rgba(47,47,47,1) 100%);
height: auto;
}
}
